.item {
  display: flex;
  align-items: center;
  padding: 18px 0;
  border-bottom: 1px solid var(--main-border-color);
  cursor: pointer; }

.building-icon {
  width: 16px;
  height: 16px;
  margin: 0 6px 0 16px;
  background: url('../../../assets/images/community3@2x.png') no-repeat center center;
  background-size: contain; }

.name {
  color: var(--main-text-color);
  line-height: 21px;
  font-weight: 400; }

.status {
  line-height: 18px;
  border-radius: 6px 6px 6px 0;
  background: rgba(255, 146, 0, 0.1);
  padding: 0 6px;
  color: rgba(255, 146, 0, 1);
  margin-left: 8px; }

.right-icon {
  width: 16px;
  height: 16px;
  background: url('../../../assets/images/right@2x.png') no-repeat center center;
  background-size: contain;
  margin: 0 20px 0 auto; }

.rooms {
  padding-bottom: 78px; }
